					</el-form-item>
					<el-form-item label="抄送人" prop="license" v-for="(val,valIndex) in item.appFlowPathDetailsList" :key="valIndex">
						<el-select v-model="val.sysAppUserId" @change="((val)=>{onChangeCopyAndSend(val,item,valIndex)})" filterable allow-create default-first-option placeholder="请选择抄送人" style="width:90%">
						  <el-option v-for="(k, kIndex) in copyAndSend" :key="k.userId" :label="k.userName" :value="k.userId">
