						>
						  <el-button
						    class="icon-size"
						    style="float: right;"
						    type="text"
						    icon="el-icon-remove-outline"
						    @click="delectCopyAndSend(item,valIndex)"
						  ></el-button>
						</el-tooltip>
					</el-form-item>
			</el-form-item>
        </el-form>
        <!-- form表单 -->
        <!-- 提交 start -->
        <div class="submit">
          <el-button type="primary" @click="submitForm('validateForm')">提交</el-button>
          <el-button @click="dialogClose">取消</el-button>
