        <!-- 提交 end -->
      </el-dialog>
      <!-- 新增 dialog end -->

      <!-- 编辑 dialog start -->
      <el-dialog class="dialog" title="编辑审批人/抄送人" :visible.sync="editDialogVisible" :before-close="editHandleClose" :close-on-click-modal="false">
        <!-- form表单 start -->
        <el-form :model="editValidateForm" ref="editValidateForm" label-width="100px" class="demo-ruleForm" :rules="editValidateFormRules">
			<!-- 审批流名称 start -->
			<el-form-item label="审批流名称" prop="carType">
				<el-input v-model="editDataRow.flowPathName" placeholder="请输入审批流名称"></el-input>
			 <!-- <el-select v-model="editValidateForm.spotName" filterable placeholder="请选择审批流名称" style="width: 100%">
			    <el-option v-for="(item, index) in searchScenicOptions" :key="index" :label="item.spotName" :value="item.spotId">
			    </el-option>
			  </el-select> -->
			</el-form-item>
			<!-- 审批流名称 end -->
			
			  <!-- 景区名称 start -->
			  <el-form-item label="景区名称" prop="carType">
			    <el-select v-model="modelScenicSpotName" @change="((val)=>{onChangeScenicSpotName(val,editDataRow)})" multiple filterable allow-create default-first-option placeholder="请选择景区名称" style="width: 100%">
			      <el-option v-for="(item, index) in searchScenicOptions" :key="index" :label="item.SCENIC_SPOT_NAME" :value="item.SCENIC_SPOT_ID">
			      </el-option>
			    </el-select>
			  </el-form-item>
			  <!-- 景区名称 end -->
			
			
			  <!-- 添加审批人 start -->
			  <el-form-item label="添加审批人" prop="license">
			   <el-tooltip
			     class="add-item"
			     effect="dark"
			     content="新增"
			     placement="top"
			     v-if="dialogState !== 'preview'"
			   >
			     <el-button
			       class="icon-size"
			       style="float:left;"
			       type="text"
			       icon="el-icon-circle-plus-outline"
			       @click="onAddauditor1"
			     ></el-button>
			   </el-tooltip>
			  </el-form-item>
			<el-form-item label="审核人" prop="license" v-for="(item,index) in editDataRow.appFlowPathDetailsList" :key="index">
				<el-select v-model="item.userName" @change="((val)=>{onChangeApprover(val,item,index)})" filterable allow-create default-first-option placeholder="请选择审核人" style="width:90%">
					<el-option v-for="(ele, eleIndex) in approver" :key="ele.userId" :label="ele.userName" :value="ele.userId">
					</el-option>
				</el-select>
				<el-tooltip
					class="add-item"
					effect="dark"
					content="删除"
					placement="top"
					v-if="dialogState !== 'preview'"
				>
					<el-button
					    class="icon-size"
					    style="float: right;"
					    type="text"
					    icon="el-icon-remove-outline"
					    @click="delectAuditor(item,index)"
					></el-button>
					</el-tooltip>
					<el-form-item label="添加抄送人" prop="license">
						<el-tooltip
							class="add-item"
							effect="dark"
							content="新增"
							placement="top"
							v-if="dialogState !== 'preview'"
						>
						<el-button
							class="icon-size"
							style="float:left;"
							type="text"
							icon="el-icon-circle-plus-outline"
							@click="onAddCopySend(item,index)"
						></el-button>
					</el-tooltip>
				</el-form-item>
				<el-form-item label="抄送人" prop="license" v-for="(val,valIndex) in item.appFlowPathDetailsList" :key="valIndex">
					<el-select v-model="val.userName" @change="((ele)=>{onChangeCopyAndSend1(ele,val,valIndex)})" filterable allow-create default-first-option placeholder="请选择抄送人" style="width:90%">
						<el-option v-for="(k, kIndex) in copyAndSend" :key="kIndex" :label="k.userName" :value="k.userId">
						</el-option>
					</el-select>
					<el-tooltip
							class="add-item"
							effect="dark"
							content="删除"
							placement="top"
							v-if="dialogState !== 'preview'"
						>
						<el-button
							class="icon-size"
							style="float: right;"
							type="text"
							icon="el-icon-remove-outline"
							@click="delectCopyAndSend(item,valIndex)"
						></el-button>
					</el-tooltip>
				</el-form-item>
			</el-form-item>
        </el-form>
        <!-- form 表单 end -->
        <!-- 提交 start -->
        <div class="submit">
			<el-button type="primary" @click="submitForm('editValidateForm')">提交</el-button>
          <!-- <el-button type="primary" @click="editSubmitForm('editValidateForm')">提交</el-button> -->
          <el-button @click="editDialogClose">取消</el-button>
        </div>
        <!-- 提交 end -->
      </el-dialog>
      <!-- 编辑 dialog end -->
      <!-- dialog对话框 end -->
    </el-container>
  </div>
</template>

<script>
// import JsonExcel from 'vue-json-excel'
import FileSaver from 'file-saver'
import XLSX from 'xlsx'

import { formatMonth, createUrl, formatDate } from "../utils/utils.js"
export default {
  name: 'SettlementFlow',
  data() {
    /**
   * 公共验证函数(验证数字值)
   * @params message - 提示信息
   */
    function checkNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          // 如果值为空，可以回调
          callback();
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填数字值)
    * @params message - 提示信息
    */
    function checkRequireNumberMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        }

        if (isNaN(Number(value))) {
          callback(new Error(name + '必须为数字值'));
        } else {
          callback();
        }
      }
    }

    /**
    * 公共验证函数(验证必填值)
    * @params message - 提示信息
    */
    function checkRequireMethod(name = "") {
      // 返回验证函数
      return (rule, value, callback) => {
        if (!value) {
          return callback(new Error(name + '不能为空'));
        } else {
          callback();
        }
      }
    }

    return {
		approveDialogVisible:false,
		appRoverData:[],//审批人
		  showTable: true, // 是否显示表格
		  tableFields: { //导出Excel表格的表头设置
			"审批流名称":'flowPathName',
			"景区名称": "SCENIC_SPOT_NAME",
			"添加审批人":'addPrev',
		  },
		  tableData: [], // 表格数据
		  tableLoading: false,
		  searchScenicOptions: [], // 搜索的景区选项
		  exportTableData: [], // 导出的全部表格数据
		  downloadLoading: false, // 导出excel按钮的加载动画

		  currentPage: 1, // 当前页数,
		  pageSize: 10, // 每页显示条数

		  dialogVisible: false, // 是否显示添加dialog对话框
		  editDialogVisible: false, // 是否显示编辑dialog对话框
		  // detailDialogVisible: false, // 是否显示详情dialog对话框
		
		 flowPathNameOptions:[
			 {type: '1',flowPathName: '故障审批'},
			  {type: '2',flowPathName: '配件审批'}
		 ],
		  dialogState: "", // dialog是新增还是编辑表单，新增为'add'，编辑为'edit'

		  selectDateState: "1", // 日期统计方式（月，日，年）
		  dateType: "daterange", // 日期类型（月，日，年）
		  valueFormat: "yyyy-MM-dd", // 日期格式

		  searchValidateForm: {
			robotCode: "", // 机器人编号
			SCENIC_SPOT_ID: "", // 景区id
			faultName: "", // 故障名称
			faultTimeType: "", // 故障时间类型
			startEndDate: "", // 起止日期
			warrantyState: "", // 质保状态
			faultState: "", // 故障状态
			signState: "", // 配件签收状态
		  }, // 搜索表单验证对象
		  searchVF: {
			robotCode: "", // 机器人编号
			spotId: "", // 景区id
			faultName: "", // 故障名称
			faultTimeType: "", // 故障时间类型
			startEndDate: "", // 起止日期
			warrantyState: "", // 质保状态
			faultState: "", // 故障状态
			signState: "", // 配件签收状态
		  }, // 搜索需要提交的表单
		  validateForm: {}, // 表单验证对象
		  editValidateForm: {}, // 表单验证对象

		  searchPartnerCompanyName: "", // 搜索的公司
		  searchScenicName: "", // 搜索的景区
		  searchSelectDateState: "1", // 搜索的日期统计方式
		  searchStartEndDate: "", // 搜索的起止时间
		  searchPayState: "", // 搜索的支付状态

		  companys: [], // 公司列表
		  companyState: '', // 当前请选择公司

		  scenics: [], // 景区列表
		  scenicState: '', // 当前请选择景区

		  downloadUrl: "",

		  total: 0,

		  currentEditRowId: null, // 当前编辑的行id

		  uploadDialogImageUrl: '',
		  uploadDialogVisible: false,
		  uploadDisabled: false, // 是否隐藏下载和删除按钮

		  limitUpload: 1,
		  file: null, // 上传的Excel文件
		  fileTemp: null, // 上传的临时Excel文件
		  modelScenicSpotName:[],

		  // 搜索表单规则
		  searchValidateFormRules: {

		  },

		  // 提交表单规则
		  validateFormRules: {
			// 机器人数量
			flowPathName: [
			  { validator: checkRequireMethod('审批流名称'), required: true,trigger: ['blur', 'change'] }
			],
			SCENIC_SPOT_NAME:[
				{ validator: checkRequireMethod('景区名称'), required: true,trigger: ['blur', 'change'] }
			],
		  },

		  // 提交编辑表单规则
		  editValidateFormRules: {},
		//审核人列表
		auditorList:[],
		approver:[],
		copyAndSend:[],
		auditorIndex:0,
		editDataRow:{},
		flowPathName:'',
		scenicSpotIdList:[],
		scenicSpotIdList1:[],
		scenicSpotIdList2:[],
		approverIndex:0,
    }
  },
  created() {
    this.getValidateForm()
    this.login()
    this.getScenicList()
	//获取表格数据
    this.initTableData()
	this.approverList()
  },
  beforeMount() {

  },
  mounted() {

  },
  methods: {
	  //获取审批人
	  async approverList(){
		  const { data: res } = await this.$http.get("/system/sys_app_flow_path/reviewedDrop")
		  if (res.state == "200") {
		    console.log(res);
			//审批人
			this.approver = res.data
			//抄送人
			this.copyAndSend = res.data
		    return res
		  } else {
		    return
		  }
	  },
    // 获取表单验证对象
    getValidateForm() {
      var validateForm = {}
      for (var item in this.tableFields) {
        validateForm[this.tableFields[item]] = ""
      }
      this.validateForm = validateForm
      // console.log(this.validateForm);
    },

    // 登录(必须)
    async login() {
      const { data: res } = await this.$http.post("/loginSystem?username=houjingchen&password=houjingchen")
    },


    /**
     * 获取景区列表（搜索时景区列表）
     */
    async getScenicList() {
		// console.log(companyId)
      // if (!companyId) {
      //   companyId = null
      // }
      // 这里使用的是`签约信息`中的`景区列表`接口
      const { data: res } = await this.$http.get("/system/robot_time_statistics/spotList", {
        // params: {
        //   companyId: companyId
        // }
      })
      this.searchScenicOptions = res.data
      // this.receivingOptions = res.data
      return res
    },

    /**
     * 初始化表格数据
     */
    async initTableData() {
      var spotId = this.searchVF.spotId
      this.tableLoading = true
      const { data: res } = await this.$http.get("/system/sys_app_flow_path/getSysAppFlowPathList", {
        params: {
          // spotId: spotId,
		  flowPathName:'',
		  scenicSpotIds:spotId,
          // type: type,
          pageNum:this.currentPage,// 页码
          pageSize: this.pageSize // 每页条数
        }
      })
	  
      console.log(res);
      this.tableData = res.data
      this.total = res.totals

      this.tableLoading = false
    },

    /**
     * 新增表格数据
     */
    async addTableData() {
		console.log(this.validateForm)
		var scentcSpotId = this.validateForm.SCENIC_SPOT_NAME+ ''
		var flowPathName = this.validateForm.flowPathName
		var appFlowPathDetailsList = {
			appFlowPathDetailsList: this.auditorList
		}
		this.validateForm = {
			scenicSpotIds:scentcSpotId,
			flowPathName:flowPathName,
			appFlowPathDetailsList:this.auditorList
		}
		var appFlowPathDetailsList = JSON.stringify(this.auditorList)
		const { data: res } = await this.$http({
			method:"post",
			url:"/system/sys_app_flow_path/addSysAppFlowPath",
			data:this.validateForm,
			dataType: "json"
		})
	  // console.log(res)
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: 'success'
        });
        this.initTableData()
