    /**
     * 修改表格数据
     */
    async editTableData() {
		var numReg = /^[0-9]*$/
		var numRe = new RegExp(numReg)
		var arrScenicSpotId = []
		this.scenicSpotIdList1.forEach(item => {
			if(numRe.test(item)){
				arrScenicSpotId.push(item)
			}
		})
		this.scenicSpotIdList.push(...arrScenicSpotId);
		var arr2 = this.scenicSpotIdList2
		let set = new Set(arr2);
		arr2 = Array.from(set);
		var arr1 = this.scenicSpotIdList
		let set1 = new Set(arr1);
		arr1 = Array.from(set1);
		
		if(this.scenicSpotIdList == ''){
			this.editDataRow.scenicSpotIds = arr2 + ''
		}else{
			this.editDataRow.scenicSpotIds = arr1 + ''
		}
	  const { data: res } = await this.$http({
	  	method:"post",
	  	url:"/system/sys_app_flow_path/editSysAppFlowPath",
	  	data:{
			...this.editDataRow,
			id:this.currentEditRowId
		},
	  	dataType: "json"
	  })
      if (res.state == "200") {
        this.$message({
          message: res.msg,
          type: 'success'
        });
        this.initTableData()
		this.auditorList = []
      } else {
        this.$message({
          message: res.msg,
          type: 'error'
        });
      }
    },


    /**
     * 导出Excel表格的全部数据
     */
    async getExportTableData() {
      var spotId = this.searchVF.spotId
      if (spotId == "" || !spotId) {
        spotId = null
      }

      var startEndDate = this.searchVF.startEndDate
      var beginTime, endTime
      if (startEndDate == "" || !startEndDate) {
        // beginDate = formatMonth()
        // endDate = formatMonth()
        beginTime = null
        endTime = null
      } else {
        beginTime = this.searchVF.startEndDate[0]
        endTime = this.searchVF.startEndDate[1]
      }

      var type = this.searchVF.type
      if (type == "" || !type) {
        type = null
      }

      const { data: res } = await this.$http.get("/system/factory_send/sendList", {
        params: {
          spotId: spotId,
          beginDate: beginTime,
          endDate: endTime,
          type: type,
        }
      })
      if (res.state == "200") {
        this.exportTableData = res.data
      } else {
        return
      }
    },
